"use client";
import { useEffect, useState } from "react";
import { Button } from "../components/ui/button";

const Navbar = () => {
  const [variant, setVariant] = useState("dark");

  useEffect(() => {
    const sections = document.querySelectorAll("section[data-nav]");

    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            setVariant(entry.target.dataset.nav);
          }
        });
      },
      { threshold: 0.6 }
    );

    sections.forEach((section) => observer.observe(section));

    return () => observer.disconnect();
  }, []);

  const isLight = variant === "light";

  return (
    <nav
      className={`fixed top-0 left-0 z-50 w-full px-16 py-8 transition-colors duration-300
        ${isLight ? "text-black" : "text-white"}
      `}
    >
      <div className="flex items-center justify-between">
        <a href="#" className="flex items-center gap-2">
          <img src="/Tess_BW.svg" className="h-8 w-8 rounded-2xl" />
          TESSERACT
        </a>

        <Button
          variant="outline"
          className={isLight ? "border-black text-black" : "border-white text-white"}
        >
          Let's Talk!
        </Button>
      </div>
    </nav>
  );
};

export default Navbar;
